<template>
    <div class="page">
        <div class="page-header">
            <a-breadcrumb>
                <a-breadcrumb-item>
                    <router-link to="/"><a-icon type="dashboard" /> </router-link></a-breadcrumb-item>
                <a-breadcrumb-item></a-breadcrumb-item>
                <a-breadcrumb-item></a-breadcrumb-item>
            </a-breadcrumb>
            <h2></h2>
        </div>
        <div class="page-content">
            <a-card>
                <a-form-item label="" :label-col="{ span: 2 }" :wrapper-col="{ span: 12 }">
                    <a-input placeholder="" v-model="quoted.subject"/>
                </a-form-item>
                <a-form-item label="" :label-col="{ span: 2 }" :wrapper-col="{ span: 22 }">
                    <a-input :style="{width:'300px'}" v-model="quoted.customer" class="mr-5" placeholder=""/>
                    <a-input :style="{width:'180px'}" v-model="quoted.mobile" placeholder=""/>
                </a-form-item>
                <a-form-item label="" :label-col="{ span: 2 }" :wrapper-col="{ span: 22 }">
                    <a-row :gutter="4" class="row-striped">
                        <a-col :span="6"></a-col>
                        <a-col :span="4"></a-col>
                        <a-col :span="2"></a-col>
                        <a-col :span="2"></a-col>
                        <a-col :span="2">/</a-col>
                        <a-col :span="2"></a-col>
                        <a-col :span="2"></a-col>
                        <a-col :span="2"></a-col>
                        <a-col :span="2"></a-col>
                    </a-row>
                    <template v-for="(item,index) in quotedItem">
                        <a-row :gutter="4" class="row-striped">
                            <a-col :span="6">{{item.content}}</a-col>
                            <a-col :span="4">{{item.crafts}}</a-col>
                            <a-col :span="2">{{item.spec}}</a-col>
                            <a-col :span="2">{{item.nums}}</a-col>
                            <a-col :span="2">{{item.area}}</a-col>
                            <a-col :span="2">{{item.unit}}</a-col>
                            <a-col :span="2">{{item.price}}</a-col>
                            <a-col :span="2">{{item.total}}</a-col>
                            <a-col :span="2">
                                <a-button type="danger" icon="delete"  @click="delItem(index)" size="small"></a-button>
                            </a-col>
                        </a-row>
                    </template>
                    <div :style="{marginTop:'10px'}">
                        <a-popover trigger="click" v-model="popVisible">
                            <template slot="content">
                                <div style="width: 1000px">
                                    <a-row :gutter="4" style="margin-bottom: 5px">
                                        <a-col :span="6"></a-col>
                                        <a-col :span="5"></a-col>
                                        <a-col :span="3"></a-col>
                                        <a-col :span="1"></a-col>
                                        <a-col :span="2">/</a-col>
                                        <a-col :span="1"></a-col>
                                        <a-col :span="2"></a-col>
                                        <a-col :span="2"></a-col>
                                    </a-row>
                                    <a-row :gutter="4">
                                        <a-col :span="6">
                                            <a-input v-model="item.content" placeholder=""/>
                                        </a-col>
                                        <a-col :span="5">
                                            <a-select
                                                    v-model="item.crafts"
                                                    style="width: 100%"
                                                    showSearch
                                                    placeholder=""
                                                    :maxTagCount="10"
                                                    @change="setProduct"
                                            >
                                                <a-select-option
                                                        v-for="item in products"
                                                        :key="item.pid"
                                                        :value="item.pid">{{item.title}}</a-select-option>
                                            </a-select>
                                        </a-col>
                                        <a-col :span="3"><a-input placeholder="" v-model="item.spec"/></a-col>
                                        <a-col :span="1"><a-input placeholder="" v-model="item.nums"/></a-col>
                                        <a-col :span="2"><a-input placeholder="" v-model="item.area"/></a-col>
                                        <a-col :span="1"><a-input placeholder="" v-model="item.unit"/></a-col>
                                        <a-col :span="2"><a-input placeholder="" v-model="item.price"/></a-col>
                                        <a-col :span="2"><a-input placeholder="" v-model="item.total"/></a-col>
                                        <a-col :span="2">
                                            <a-button type="primary"
                                                      @click="addItem"
                                                      :loading="addLoading"
                                                      block></a-button>
                                        </a-col>
                                    </a-row>
                                </div>
                            </template>
                            <a-button type="dashed" icon="plus" block></a-button>
                        </a-popover>
                    </div>
                </a-form-item>
                <a-form-item>
                <a-row>
                    <a-col :span="2" class="ant-form-item-label"><label></label></a-col>
                    <a-col :span="3">
                        <a-input value="0.00"/>
                    </a-col>
                    <a-col :span="2" class="ant-form-item-label"><label></label></a-col>
                    <a-col :span="3">
                        <a-select
                                showSearch
                                placeholder=""
                                style="width: 100%"
                                @change="setUid"
                                :maxTagCount="5"
                        >
                            <a-select-option :value="item.uid" v-for="item in user" :key="item.uid">
                                {{item.username}}
                            </a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :span="2" class="ant-form-item-label"><label></label></a-col>
                    <a-col :span="3">
                        <a-date-picker/>
                    </a-col>
                </a-row>
                </a-form-item>
                <a-row style="margin-top: 14px">
                    <a-col :span="8" :offset="2">
                        <a-button type="primary" icon="save" class="mr-5"></a-button>
                        <a-button class="mr-5" icon="copy"></a-button>
                        <a-button icon="export"></a-button>
                    </a-col>
                </a-row>
            </a-card>
            <a-modal
                    title=""
                    :visible="visible"
                    @ok="addItem"
                    :confirmLoading="confirmLoading"
                    @cancel="hideModal"
                    width="600px"
            >
                <div>
                </div>
            </a-modal>
        </div>
    </div>
</template>

<script>
    import utils  from '../../plugins/utils'
    export default {
        data() {
            return {
                visible:false,
                popVisible:false,
                confirmLoading:false,
                addLoading:false,
                quoted:{subject:'',customer:'',mobile:'',item:[],total:'',pm:'',dateline:''},
                item:{content:'',crafts:'',spec:'',nums:'',area:'',price:'',unit:'',total:''},
                quotedItem:utils.getItem('quotedItem'),
                user:[],
                products:[]
            };
        },
        created(){
            this.$axios.get('get_user.html').then((res)=>{
                this.user = res.data.data
            });
            this.$axios.get('get_products.html').then((res)=>{
                this.products = res.data.data
            })
        },
        methods:{
            showModal(){
                this.visible = true
            },
            hideModal(){
                this.visible =false
            },
            addItem(){
                this.addLoading = true
                if(!this.item.content || !this.item.crafts ||!this.item.spec || !this.item.nums ||!this.item.nums){
                    this.$message.error('');
                }else {
                    if(this.quotedItem === null){
                        this.quotedItem = []
                    }
                    if(this.quotedItem.length===0){
                        this.quotedItem.push(this.item)
                    }else {
                        this.quotedItem = this.quotedItem.concat(this.item)
                    }
                    this.popVisible = false
                    utils.setItem('quotedItem',this.quotedItem)
                    this.item = {content:'',crafts:'',spec:'',nums:'',area:'',price:'',unit:'',total:''}
                }
                this.addLoading = false
            },
            delItem(e){
                this.quotedItem.splice(e,1);
                utils.setItem('quotedItem',this.quotedItem)
            },
            setUid(){

            },
            setProduct(pid){
                let proItem;
                for (let i of this.products){
                    if(i.pid === pid){
                        proItem = i;
                        break
                    }
                }
                this.item.price = proItem.out_price
                this.item.unit = proItem.unit
            }
        }
    };
</script>